import 'core-js/features/map'
import 'core-js/features/set'
import React from 'react'
import ReactDOM from 'react-dom'
import bridge from '@vkontakte/vk-bridge'
import App from './App'

import ava from './img/ava.png'
import intro1 from './img/intro1.png'
import intro2 from './img/intro2.png'

ReactDOM.render(<App />, document.getElementById('root'))
// if (process.env.NODE_ENV === "development") {
  import('./eruda').then(({ default: eruda }) => {})
// }
bridge.send("VKWebAppStorageGet", {"keys": ["greeting"]})
.then(async (res) => {
  if (res.keys[0].value === 'true') {
    if (window.Image) {
      new window.Image().src = ava
      new window.Image().src = intro1
      new window.Image().src = intro2
    }
  }
})
bridge.send('VKWebAppInit')