import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, Epic, Tabbar, TabbarItem } from '@vkontakte/vkui';
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline';
import Icon28More from '@vkontakte/icons/dist/28/more';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Profile from './panels/Profile';
import './styles.css';

class App extends React.Component {
  constructor (props) {
    super(props);

    this.state = {
      activeStory: 'home',
      popout: null
    };
	  this.onStoryChange = this.onStoryChange.bind(this);
  }

  componentDidMount () {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
        document.body.attributes.setNamedItem(schemeAttribute);
			}
    });
	};

  onStoryChange (e) {
    this.setState({ activeStory: e.currentTarget.dataset.story })
  }

  render () {
    return (
      <Epic activeStory={this.state.activeStory} tabbar={
        <Tabbar>
          <TabbarItem
            style={{cursor: 'pointer'}}
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'home'}
            data-story="home"
          ><Icon28HomeOutline /></TabbarItem>
          <TabbarItem
            style={{cursor: 'pointer'}}
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'profile'}
            data-story="profile"
          ><Icon28More /></TabbarItem>
        </Tabbar>
      }>
        <View id="home" activePanel="home" popout={this.state.popout}>
			<Home setPopout={popout => this.setState({ popout })} id='home' />
        </View>
        <View id="profile" activePanel="profile">
			<Profile id='profile' />
        </View>
      </Epic>
    )
  }
}

export default App;